<template>
    <div>
        <pre class="pre-detail">
    1、设置、存Storage，localStorage和sessionStorage参数一致，括号里是一个对象，
    {
        key:'',  //存的key名称，必填
        value:'',  //key对应的值，必填，可为初始类型和应用类型
        expires:number,  //时间超时期限，选填
    }
    示例：
        localStorage：this.$storage.setL({key:'key',value:''});
        sessionStorage：this.$storage.setS({key:'key',value:''});
    2、获取storage的值,括号内是存储的key的名称
    示例：
        localStorage：this.$storage.getL('keyName');
        sessionStorage：this.$storage.getS('keyName');
    3、清除，销毁storage
     示例：
        localStorage：
                销毁单个：this.$storage.clearL('keyName');
                销毁全部：this.$storage.clearL();
        sessionStorage：
                销毁单个：this.$storage.clearS('keyName');
                销毁全部：this.$storage.clearS();
        </pre>
    </div>
</template>

